html {
  font-size: 100%;
}

body {
  background-attachment: fixed;
  background-color: #ffffff;
  color: #777777;
  font-family: 'Nunito', sans-serif;
  font-size: 16px;
  line-height: 1.642;
  overflow-x: hidden;
  transition: inherit;
  -webkit-font-smoothing: antialiased;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
body.body_overlay {
  position: fixed;
  transform: translate(0, 0);
  width: 100%;
}
body.body_overlay:before {
  background-color: rgba(0, 108, 112, 0.5);
  bottom: 0;
  content: '';
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  -webkit-transition: all 0.8s ease;
  -moz-transition: all 0.8s ease;
  -ms-transition: all 0.8s ease;
  -o-transition: all 0.8s ease;
  transition: all 0.8s ease;
  transform: translate(360px, 0);
}
img {
  max-width: 100%;
}

p {
  font-size: 14px;
  color: #484848;
  font-weight: 400;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

iframe {
  border: none;
  width: 100%;
}

a {
  color: #555555;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
}
a:hover,
a:focus {
  color: #333333;
  text-decoration: none;
  outline: none;
}
a img {
  border: none;
}
.form-control {
  height: 50px;
}
.form-control:active,
.form-control:focus {
  box-shadow: none;
  outline: none;
}

iframe {
  border: none;
}

::selection {
  background: #222222;
  color: #ffffff;
  text-shadow: none;
}

::-moz-selection {
  /* Firefox */
  background: #222222;
  color: #ffffff;
  text-shadow: none;
}

::-webkit-selection {
  /* Safari */
  background: #222222;
  color: #ffffff;
  text-shadow: none;
