.mk-fullscreen-search-overlay
  #mk-fullscreen-search-wrapper
  #mk-fullscreen-searchform
  input[type='submit'],
.mk-fullscreen-search-overlay
  #mk-fullscreen-search-wrapper2
  #mk-fullscreen-searchform2
